/**
 *
 查询完善信息学员列表
 @param { Number } pageSize
 @param { Number } pageNo
 @param { Number } status 查询状态 0 未分配顾问 1未完善 2已完善 3不跟进
 @param { String } payUid 学员uid
*/
export function queryCompleteStudentList (params) {
  return axios.request({
    url: 'crmStudent/queryCompleteStudentList',
    method: 'post',
    data: params
  })
}

