<!--顾问维护记录添加弹框-->
<template>
  <Modal
      :mask-closable="false"
      title="添加维护记录"
      v-model="modalValue"
      width="700px">
      <Form ref="recordAddForm" :model="formData" :rules="ruleValidate" :label-width="100" label-position="right" :show-message="showValidateMsg">
        <FormItem label="维护任务" prop="tag">
          <template v-if="data.needFollowTaskList && data.needFollowTaskList.length">
            <Tag color="primary" v-for="(v, k) in data.needFollowTaskList" :key="k">{{v.taskName}}</Tag>
          </template>
          <Tag v-else color="primary">其他</Tag>
        </FormItem>
        <Row>
          <Col :span="12">
            <FormItem label="沟通方式" prop="followType">
              <Select v-model="formData.followType" style="width: 120px">
                <Option :value="k" v-for="(v, k) in data.followRecordCommModeMap" :key="k">{{v}}</Option>
              </Select>
            </FormItem>
          </Col>
          <Col :span="12">
            <FormItem label="是否有效沟通" prop="isEffectiveCommunication">
              <RadioGroup v-model="formData.isEffectiveCommunication" @on-change="isEffectiveCommunicationChange">
                <Radio :label=1>是</Radio>
                <Radio :label=0>否</Radio>
              </RadioGroup>
            </FormItem>
          </Col>
        </Row>
        <template v-if="formData.isEffectiveCommunication === 1">
          <FormItem label="用户来源" prop="userSource">
            <Select v-model="formData.userSource" style="width: 120px">
              <Option :value="k" v-for="(v, k) in data.followRecordUserSource" :key="k">{{v}}</Option>
            </Select>
          </FormItem>
          <div v-show="formData.userSource === '3'">
            <FormItem prop="activityName" style="margin-bottom:3px">
              <Input v-model="formData.activityName" :maxlength=20 placeholder="请输入用户参与的活动、短期班、直播名称"></Input>
            </FormItem>
            <div class="text-num" >{{formData.activityName.length}}/20</div>
          </div>
          <FormItem label="沟通内容" prop="content" style="margin-bottom:3px">
            <Input v-model="formData.content" type="textarea" :autosize="{minRows: 2,maxRows: 5}" :maxlength=300 placeholder="请输入沟通内容"></Input>
          </FormItem>
          <div class="text-num">{{formData.content.length}}/300</div>
          <Row>
            <Col :span="12">
              <FormItem label="达成结果" prop="resultArr">
                <Cascader :data="data.followRecordCommResultMap" v-model="formData.resultArr" @on-change="changeResult"></Cascader>
              </FormItem>
            </Col>
            <Col :span="12">
              <FormItem label="是否预留名额" prop="isQuotaReserved">
                <RadioGroup v-model="formData.isQuotaReserved">
                  <Radio :label=1>是</Radio>
                  <Radio :label=0>否</Radio>
                </RadioGroup>
              </FormItem>
            </Col>
          </Row>
          <div v-show="formData.resultArr[0] === 100">
            <FormItem label="" prop="followFlag" style="margin-bottom:3px">
              <Input v-model="formData.followFlag" type="textarea" :autosize="{minRows: 2,maxRows: 5}" :maxlength=300 placeholder="请输入自定义达成结果"></Input>
            </FormItem>
            <div class="text-num" >{{formData.followFlag.length}}/300</div>
          </div>
          <div v-show="formData.resultArr[1] === 100">
            <FormItem label="" prop="noIntentionReasonExtra" style="margin-bottom:3px">
              <Input v-model="formData.noIntentionReasonExtra" type="textarea" :autosize="{minRows: 2,maxRows: 5}" :maxlength=300 placeholder="请输入无意向报名原因"></Input>
            </FormItem>
            <div class="text-num" >{{formData.noIntentionReasonExtra.length}}/300</div>
          </div>
          <FormItem label="客户分级" prop="customerLevel">
            <Select v-model="formData.customerLevel" style="width: 120px">
              <Option :value="item" v-for="item in data.followRecordCustomerLevel" :key="item">{{item}}</Option>
            </Select>
          </FormItem>
          <FormItem label="意向记录">
            <Row>
              <Col :span="8">意向学期 <Input v-model="formData.intendedTerm" :maxlength=50 style="width: 120px"></Input></Col>
              <Col :span="8">意向学科 <Input v-model="formData.intendedSubject" :maxlength=50 style="width: 120px"></Input></Col>
              <Col :span="8">意向班型 <Input v-model="formData.intendedClassType" :maxlength=50 style="width: 120px"></Input></Col>
            </Row>
            <Row style="margin-top: 10px">
              <Col :span="8">意向时间 <Input v-model="formData.intendedTime" :maxlength=50 style="width: 120px"></Input></Col>
              <Col :span="8">意向教师 <Input v-model="formData.intendedTeacher" :maxlength=50 style="width: 120px"></Input></Col>
              <Col :span="8">意向教学点 <Input v-model="formData.intendedTeachingPoint" :maxlength=50 style="width: 120px"></Input></Col>
            </Row>
          </FormItem>
          <FormItem label="不参诊原因" prop="noDiagnosisReason">
            <Select v-model="formData.noDiagnosisReason" @on-change="diagnosisReasonChange" style="width: 120px" clearable>
              <Option :value="k" v-for="(v, k) in data.followRecordNoDiagnosisReason" :key="k">{{v}}</Option>
            </Select>
          </FormItem>
          <div v-show="formData.noDiagnosisReason === '100'">
            <FormItem label="" prop="noDiagnosisReasonExtra" style="margin-bottom:3px">
              <Input v-model="formData.noDiagnosisReasonExtra" type="textarea" :autosize="{minRows: 2,maxRows: 5}" :maxlength=300 placeholder="请输入其他不参诊原因"></Input>
            </FormItem>
            <div class="text-num" >{{formData.noIntentionReasonExtra.length}}/300</div>
          </div>
        </template>
        <FormItem label="维护预约" prop="isAppointmentFollow">
          <RadioGroup v-model="formData.isAppointmentFollow" @on-change="changeStatus">
            <Radio :label=1>是</Radio>
            <Radio :label=0>否</Radio>
          </RadioGroup>
        </FormItem>
        <div v-show="formData.isAppointmentFollow === 1">
          <FormItem label="维护时间" prop="timeValue" >
            <DatePicker type="datetime" placeholder="选择时间" v-model="formData.timeValue" format="yyyy-MM-dd HH:mm:ss" @on-change="changeTime" :editable="false"></DatePicker>
          </FormItem>
          <FormItem label="预约内容" prop="appointmentContent" style="margin-bottom:3px">
            <Input v-model="formData.appointmentContent" type="textarea" :autosize="{minRows: 2,maxRows: 5}" :maxlength=300 placeholder="请输入预约内容"></Input>
          </FormItem>
          <div class="text-num">{{formData.appointmentContent.length}}/300</div>
        </div>
      </Form>
      <div slot="footer">
        <div style="text-align: center">
          <Button @click="saveConfirm" type="primary" style="margin-left: 10px">提交</Button>
          <Button @click="tempSave" style="margin-left: 10px">保存</Button>
          <Button @click="clear">清空</Button>
        </div>
      </div>
    </Modal>
</template>

<script>
import { followTmpSave, followTmpGetActive, followTmpDelete } from '@/api/crmStudent'
import { XesAnalyticsTrack } from '@/libs/tal-sensors'
const formData = {
  id: '',
  userSource: '', // 用户来源
  activityName: '', // 活动名称
  followType: '', // 沟通方式
  isEffectiveCommunication: 1, // 是否有效沟通
  content: '', // 沟通内容
  followResult: '', // 达成结果
  followFlag: '', // 自定义达成结果
  noIntentionReason: '', // 无意向报名原因
  noIntentionReasonExtra: '', // 无意向报名原因选其他时填写
  isQuotaReserved: '', // 是否预留名额
  customerLevel: '', // 客户分级
  intendedTerm: '', // 意向学期
  intendedSubject: '', // 意向学科
  intendedClassType: '', // 意向班型
  intendedTime: '', // 意向时间
  intendedTeacher: '', // 意向教师
  intendedTeachingPoint: '', // 意向教学点
  noDiagnosisReason: '', // 不参诊原因
  noDiagnosisReasonExtra: '', // 不参诊原因其他时填写
  isAppointmentFollow: 0,
  appointmentTime: '', // 预约维护时间
  appointmentContent: '', // 预约维护内容
  timeValue: '',
  resultArr: []
}
export default {
  name: 'MaintainRecordAdd',
  props: {
    show: {
      type: Boolean,
      default: false
    },
    uid: Number,
    data: {
      type: Object,
      default: () => {
        return {}
      }
    },
    tempData: {
      type: Object
    },
    from: String // 从哪里维护的 0: 提醒状态栏 1:沟通记录
  },
  data () {
    const validateActivityName = (rule, value, callback) => {
      if (this.formData.userSource === '3' && !value) {
        callback(new Error('请输入用户参与的活动/短期班/直播名称'))
      } else {
        callback()
      }
    }
    const validateFollowFlag = (rule, value, callback) => {
      if (this.formData.resultArr[0] === 100 && !value) {
        callback(new Error('请输入自定义达成结果'))
      } else {
        callback()
      }
    }
    const validateNoIntentionReasonExtra = (rule, value, callback) => {
      if (this.formData.resultArr[1] === 100 && !value) {
        callback(new Error('请输入无意向报名原因'))
      } else {
        callback()
      }
    }
    const validateNoDiagnosisReasonExtra = (rule, value, callback) => {
      if (this.formData.noDiagnosisReason === '100' && !value) {
        callback(new Error('请输入其他不参诊原因'))
      } else {
        callback()
      }
    }
    const validateTimeValue = (rule, value, callback) => {
      if (this.formData.isAppointmentFollow === 1 && !value) {
        callback(new Error('请选择维护时间'))
      } else {
        callback()
      }
    }
    const validateAppointmentContent = (rule, value, callback) => {
      if (this.formData.isAppointmentFollow === 1 && !value) {
        callback(new Error('请填写预约内容'))
      } else {
        callback()
      }
    }
    return {
      showValidateMsg: false,
      formData: Object.assign({}, formData),
      ruleValidate: {
        followType: [
          { required: true, message: '请选择沟通方式' }
        ],
        isEffectiveCommunication: [
          { required: true, message: '请选择是否有效沟通' }
        ],
        activityName: [
          { validator: validateActivityName }
        ],
        followFlag: [
          { validator: validateFollowFlag }
        ],
        noIntentionReasonExtra: [
          { validator: validateNoIntentionReasonExtra }
        ],
        resultArr: [
          {
            validator: (rule, value, callback) => {
              if (this.formData.isEffectiveCommunication === 1 && (!value || !value.length)) {
                callback(new Error('请选择达成结果'))
              } else {
                callback()
              }
            }
          }
        ],
        isQuotaReserved: [
          {
            validator: (rule, value, callback) => {
              if (this.formData.isEffectiveCommunication === 1 && value === '') {
                callback(new Error('请选择是否预留名额'))
              } else {
                callback()
              }
            }
          }
        ],
        customerLevel: [
          {
            validator: (rule, value, callback) => {
              if (this.formData.isEffectiveCommunication === 1 && value === '') {
                callback(new Error('请选择客户分级'))
              } else {
                callback()
              }
            }
          }
        ],
        noDiagnosisReasonExtra: [
          { validator: validateNoDiagnosisReasonExtra }
        ],
        timeValue: [
          { validator: validateTimeValue }
        ],
        appointmentContent: [
          { validator: validateAppointmentContent }
        ]
      }
    }
  },
  methods: {
    tempSave () {
      this.formData.pyUid = this.uid
      followTmpSave(this.formData).then(() => {
        this.$Message.success('保存成功')
      }).catch(err => {
        this.$Message.error('保存失败:' + err.message)
      })
    },
    saveConfirm () {
      this.showValidateMsg = true
      this.$refs['recordAddForm'].validate((valid) => {
        if (valid) {
          this.$emit('success', this.formData)
        }
      })
    },
    clear () {
      this.$Modal.confirm({
        title: '确认',
        content: `是否确认清空？`,
        onOk: () => {
          if (this.formData.id) {
            followTmpDelete({
              tmpId: this.formData.id
            }).then(() => {
              this.$Message.success('清空成功')
              this.formData = Object.assign({}, formData)
              this.$refs['recordAddForm'].resetFields()
            }).catch(err => {
              this.$Message.error('清空失败:' + err.message)
            })
          } else {
            this.$refs['recordAddForm'].resetFields()
          }
        }
      })
    },
    changeTime (time) {
      if (time) {
        time = time.replace(new RegExp(/-/gm), '/')
        this.formData.appointmentTime = (new Date(time).getTime()) / 1000
      }
    },
    setValue () {
      this.$refs['recordAddForm'].resetFields()
      if (this.formData.id) {
        followTmpDelete({
          tmpId: this.formData.id
        })
      }
      this.formData.appointmentTime = ''
    },
    diagnosisReasonChange () {
      this.formData.noDiagnosisReasonExtra = ''
    },
    changeStatus () {
      if (this.formData.isAppointmentFollow === 0) { // 来回切换时，清空数据
        this.formData.timeValue = ''
        this.formData.appointmentTime = ''
        this.formData.appointmentContent = ''
      }
    },
    changeResult (data) { // 达成结果切换选项时，清空数据
      this.formData.followResult = data[0]
      this.formData.noIntentionReason = data[1]
      this.formData.followFlag = ''
      this.formData.noIntentionReasonExtra = ''
    },
    getTempData () {
      followTmpGetActive({
        pyUid: this.uid
      }).then(data => {
        if (data && data.id) {
          if (data.userSource) {
            data.userSource = String(data.userSource)
          }
          if (data.followType) {
            data.followType = String(data.followType)
          }
          if (data.noDiagnosisReason) {
            data.noDiagnosisReason = String(data.noDiagnosisReason)
          }
          data.resultArr = [ data.followResult, data.noIntentionReason ]
          if (data.appointmentTime && data.appointmentTime !== '0') {
            data.timeValue = new Date(data.appointmentTime * 1000)
          }
          if (data.isEffectiveCommunication === null) {
            data.isEffectiveCommunication = 1
          }
          this.formData = Object.assign(this.formData, data)
        }
      }).catch(err => {
        this.$Message.error('查询缓存数据失败:' + err.message)
      })
    },
    isEffectiveCommunicationChange (data) {
      if (data === 0) {
        const _formData = {
          id: this.formData.id,
          followType: this.formData.followType,
          isEffectiveCommunication: this.formData.isEffectiveCommunication,
          isAppointmentFollow: this.formData.isAppointmentFollow,
          timeValue: this.formData.timeValue,
          appointmentTime: this.formData.appointmentTime,
          appointmentContent: this.formData.appointmentContent
        }
        this.formData = Object.assign({}, formData, _formData)
      }
    }
  },
  computed: {
    modalValue: {
      get: function () {
        if (this.show) {
          this.getTempData()
          if (this.data.needFollowTaskList) {
            // 任务维护埋点
            let task_name = ''
            this.data.needFollowTaskList.forEach(item => {
              task_name += item.taskName + ','
            })
            XesAnalyticsTrack('click_UUYa0Ong', {
              task_name: task_name.slice(0, -1),
              biz_type: this.from
            })
          }
        }
        return this.show
      },
      set: function (val) {
        this.showValidateMsg = false
        this.formData = Object.assign({}, formData)
        this.$refs['recordAddForm'].resetFields()
        this.$emit('close', val)
      }
    }
  }
}
</script>
<style>
  .text-num{
    text-align: right;
    color: #909399;
    font-size: 12px;
  }
</style>
